import React from "react";
import "./inputform.css";
import {Container, Row, Col, Form, Button} from 'react-bootstrap';

const Inputform = () => {
    if (typeof window !== `undefined`) {
    const scriptURL = 'https://script.google.com/macros/s/AKfycbxjlxOWi7uR-44e6AmYexgQ7GQMlIEAJZGbpcHm-wxoYwYudL9U/exec'
    const form = document.forms['google-sheet']
    
    function clear_inputs(){
        document.getElementById("form").reset();
    }

    try {
    form.addEventListener('submit', e => {
      e.preventDefault()
      var x = document.forms["google-sheet"]["email"].value;
      var p = document.forms["google-sheet"]["phonenumber"].value;
    if (x === "" && p === "") {
    alert("Kindly Fill in your name or number, then click on either of the 3 Properties given");
    return false;}
      fetch(scriptURL, { method: 'POST', body: new FormData(form)})
        .then(response => alert("Thanks for Contacting us..! We Will Contact You Soon...")).then(clear_inputs())
        .catch(error => console.error('Error!', error.message))
    })
    }
    catch(err){
        console.log(err)
    }
    }

    return (
    <Container className="new">
        <Row>
            <Col className="text-center">
                <h3>Let us reach out to you!</h3>
            </Col>
        </Row>
        <Row className="justify-content-center">
            <Col xs={12} lg={6} md={6}>
                <form id="form" method="post" autocomplete="off" name="google-sheet" className="formBG">
                <div className="darkBG">
                <Form.Group className="mb-3" controlId="formBasicEmail">
                <Form.Label>Full Name</Form.Label>
                <Form.Control type="name" name="name"  placeholder="Enter full name" />
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                <Form.Label>Phone Number</Form.Label>
                <Form.Control type="number" name="phonenumber" placeholder="Enter phone number" />
                <Form.Text className="text-muted">
                    We'll never share your number with anyone else.
                </Form.Text>
                </Form.Group>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                <Form.Label>Email address</Form.Label>
                <Form.Control type="email" name="email" placeholder="Enter email" />
                <input type="hidden" id="strReferrer" name="strReferrer" value="Saadiyat Grove" />
                </Form.Group>
                <Button type="submit" value="submit" className="btn">Submit</Button>
                </div>
                </form>
            </Col>
        </Row>
    </Container>
)}

export default Inputform